<template>
	<section>
		<!---->
		<template>
			<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-select v-model="value" placeholder="">
				<el-option
					v-for="item in options"
					:key="item.value"
					:label="item.label"
					:value="item.value"
					@click.native="DelFeature">
				</el-option>
			</el-select>
			</el-col>
		</template>
		<p></p>
		<template>
			<el-row> 
				<el-col :span="4" v-for="(o, index) in models" :key="o" :offset="index > 0 ? 0.5: 0">
					<div  @click="viewModel(o)" id="box">
						<el-card :body-style="{ padding: '0px' }">
							<img v-bind:src="o.picture[0]" alt="" class="image" >
							<div style="padding: 14px;">
								<span>{{o.name}}</span>
								<div class="bottom clearfix">
									<div class="price">{{ o.price }}</div>
								</div>
							</div>
						</el-card>
					</div>	
				</el-col>
			</el-row>
		</template>
		<!---->
		<!--
		 <div class="" id="box">
		<ul>
			<li v-for="v in models">
				<div @click="viewModel(v)">
					<img v-bind:src="v.src" alt="" >
					<h4>{{v.name}}</h4>
					<p>{{v.price}}</p>
				</div>
			</li>
		</ul>
	</div> 
	-->
	
		<!---->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="FormVisible" :close-on-click-modal="false">
			<el-container :model="VisibleForm" ref="VisibleForm">
				<el-aside width="200px;" height="200px">
					<el-carousel indicator-position="inside" height="100">
						<el-carousel-item v-for="item in VisibleForm.picture" :key="item">
							<div align="center"><img v-bind:src="item" class="imagedetails"></div>
						</el-carousel-item>
					</el-carousel>
				</el-aside>
				<el-container>
					<el-header><h2>{{VisibleForm.name}}</h2></el-header>
					<el-main>
						<h4>{{VisibleForm.nickName}}</h4>
						<h4>{{VisibleForm.price}} </h4>
						<h4>{{VisibleForm.number}}</h4>
						<h4>{{VisibleForm.content}}</h4>
						<h4>{{dealFeature(VisibleForm.feature)}}</h4>
					</el-main>
					<el-footer>
						<div slot="footer" class="dialog-footer" scope="scope">
							<el-button @click.native="editVersion(VisibleForm)"></el-button>
							<el-button type="danger" @click.native="DelSubmit(VisibleForm)" :loading="editLoading"></el-button>
						</div>
					</el-footer>
				</el-container>
			</el-container>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="price">
					<el-col :span="6">
						<el-input   type="number" :min="0" :max="200" v-model="editForm.price" auto-complete="off"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="" prop="total">
					<el-col :span="6">
						<el-input  type="number" :min="1" v-model="editForm.number" auto-complete="off"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="" prop="content">
					<el-input  type="textarea" v-model="editForm.content" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<template>
						<el-select v-model="editForm.feature" placeholder="">
							<el-option
								v-for="item in options"
								:key="item.value"
								:label="item.label"
								:value="item.value">
							</el-option>
						</el-select>
					</template>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

	</section>
</template>

<script>
import util from "../../common/js/util";
//import NProgress from 'nprogress'
import { getModelListPage, removeModel, editModel } from "../../api/api";

export default {
  data() {
    return {
      filters: {
        name: ""
      },
			models: [],
			allmodels:[],
      total: 0,
      page: 1,
      listLoading: false,
      FormVisible: false,
      sels: [], //
      VisibleForm: {},
      editLoading: false,
      editFormVisible: false, //
      //
      editForm: {
        goodId: "",
        name: "",
        price: 0,
        describe: "ChangeGood",
        content: "",
        poster: "",
        number: "",
        picture: [],
        shopKind: "0",
        nickName: "",
        feature: ""
      },
      options: [
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        },
        {
          value: "4",
          label: ""
        },
        {
          value: "5",
          label: ""
        },
        {
          value: "6",
          label: ""
        }
			],
			value:'',
    };
  },
  methods: {
    //
    viewModel: function(model) {
      // `this`  Vue 
      this.FormVisible = true;
      this.VisibleForm = Object.assign({}, model);
    },

    handleCurrentChange(val) {
      this.page = val;
      this.getModel();
		},
		DelFeature:function() {
			this.models = this.allmodels.filter(model => {
				if (this.value && model.feature.indexOf(this.value) == -1) return false;
				return true;
			})
		},
    //
    dealFeature: function(e) {
      switch (e) {
        case "1":
          return "";
        case "2":
          return "";
        case "3":
          return "";
        case "4":
          return "";
        case "5":
          return "";
        case "6":
          return "";
      }
    },
    //
    getModel() {
      let para = {
        page: this.page,
        name: this.filters.name,
        shopKind: 0
      };
      this.listLoading = true;
      //NProgress.start();
      getModelListPage(para)
        .then(res => {
          this.total = res.data.total;
          this.models = res.data.models;
          for (var i = 0; i < this.models.length; i++) {
            if (
              typeof this.models[i].picture == "string" &&
              this.models[i].picture.constructor == String
            )
              this.models[i].picture = this.models[i].picture.split(",");
          }
          this.allmodels = this.models;
					//console.log(this.models);
          this.listLoading = false;
          //NProgress.done();
        })
        .catch(() => {
          this.$message({
            message: "",
            type: "warning"
          });
        });
    },

    //
    editVersion: function(VisibleForm) {
      this.editFormVisible = true;
      this.editForm = Object.assign({}, VisibleForm);
      this.editForm.goodId = VisibleForm._id;
    },

    //
    editSubmit: function() {
      this.$refs.editForm.validate(valid => {
        if (valid) {
          this.$confirm("", "", {}).then(() => {
            this.editLoading = true;
            //NProgress.start();
            var user = sessionStorage.getItem("user");
            user = JSON.parse(user);
            var username = user.name;
            if (user.permission != "3" && username != this.editForm.nickName) {
              this.editLoading = false;
              //NProgress.done();
              this.$message({
                message: "",
                type: "warning"
              });
              return;
            }
            this.editForm.poster = user._id;
            this.editForm.nickName = user.name;
            this.editForm.describe = "ChangeGood";
            let para = Object.assign({}, this.editForm);
            console.log(para);
            editModel(para).then(res => {
              this.editLoading = false;
              //NProgress.done();
              this.$message({
                message: res.data.code == "1" ? "" : "",
                type: res.data.code == "1" ? "success" : "error"
              });
              this.$refs["editForm"].resetFields();
              this.editFormVisible = false;
              this.getModel();
            });
          });
        }
      });
    },

    //
    DelSubmit: function(VisibleForm) {
      this.$confirm("?", "", {
        type: "warning"
      })
        .then(() => {
          this.editLoading = true;
          //NProgress.start();

          var user = sessionStorage.getItem("user");
          user = JSON.parse(user);
          var username = user.name;
          if (user.permission != "3" && username != this.editForm.nickName) {
            this.editLoading = false;
            //NProgress.done();
            this.$message({
              message: "",
              type: "warning"
            });
            return;
          }
          let para = {
            //goodId: VisibleForm._id,
            //id : user.id,
            //describe : "DeleteGood"
            id: VisibleForm._id
          };
          removeModel(para).then(res => {
            this.editLoading = false;
            //NProgress.done();
            this.$message({
              message: res.data.code == "1" ? "" : "",
              type: res.data.code == "1" ? "success" : "error"
            });
            this.FormVisible = false;
            this.getModel();
          });
        })
        .catch(() => {});
    },

    selsChange: function(sels) {
      this.sels = sels;
    }
  },
  mounted() {
    this.getModel();
  }
};
</script>

<style scoped>
#box ul {
  display: flex;
  flex-wrap: wrap;
  max-height: 100%;
}
#box li {
  padding: 3px;
  list-style: none;
  margin-right: 15px;
  border: 1px solid #eee;
}
#box img {
  width: 200px;
  height: 150px;
}

.el-carousel__item h3 {
  color: #475669;
  font-size: 18px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}

.el-header,
.el-footer {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: center;
  line-height: 200px;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
  line-height: 160px;
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}

.price {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  height: 100%;
  display: block;
  margin: 0 auto;
}

.imagedetails {
  height: 100%;
  max-height: 300px;
  overflow: hidden;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}
</style>


