<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUser"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<template>
			<el-table :data="users" highlight-current-row v-loading="loading" style="width: 100%;">
				<el-table-column type="index" width="70">
				</el-table-column>
				<el-table-column prop="name" label="" width="120" sortable>
				</el-table-column>
				<el-table-column prop="gender" label="" width="100" :formatter="formatSex" sortable>
				</el-table-column>
				<el-table-column prop="college" label="" min-width="100" sortable>
				</el-table-column>
				<el-table-column prop="community" label="" min-width="180" sortable>
				</el-table-column>
				<el-table-column prop="tel" label="" min-width="100" sortable>
				</el-table-column>
				<el-table-column label="" width="150">
				<template scope="scope">
					<el-button size="small" @click="handlePass(scope.$index, scope.row)" :loading="passLoading"></el-button>
					<el-button type="small" size="small" @click="handleDel(scope.$index, scope.row)" :loading="refuseLoading"></el-button>
				</template>
			</el-table-column>
			</el-table>
		</template>

	</section>
</template>
<script>
	import { getRegisterUserList, passRegisterUser, refuseRegisterUser } from '../../api/api';
	//import NProgress from 'nprogress'
	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				loading: false,
				users: [
				],
				//community:"",
			}
		},
		methods: {

			formatSex: function (row, column) {
				return row.gender == "1" ? '' : row.gender == "0" ? '' : '';
			},
			//
			getUser: function () {
					var user = sessionStorage.getItem('user');
					if (user) {
						user = JSON.parse(user);
					}
				let para = {
					name: this.filters.name,
					community: user.community,
					access:0
				};
				this.loading = true;
				//NProgress.start();
				
				getRegisterUserList(para).then((res) => {
					this.users = res.data.UsersInformation;
					this.loading = false;
					//NProgress.done();
				}).catch(() => {
					this.$message({
							message: "",
							type: "warning"
						});
				});
			},
			//
			handlePass:function(index, row) {
				this.loading = true;
				let para = { id:row.id,
							access:1 };
				passRegisterUser(para).then((res) => {
					this.loading = false;
					this.$message({
						message: res.data.code == "1" ? "":"",
						type: (res.data.code == "1") ? "success" : "error"
					});

					this.getUser();
				});
				
			},

			//
			handleDel: function (index, row) {
				this.loading = true;
				let para = { id:row.id ,
							access: -1 };
				refuseRegisterUser(para).then((res) => {
					this.loading = false;

					this.$message({
						message: res.data.code == "1" ? "":"",
						type: (res.data.code == "1") ? "success" : "error"
					});

					this.getUser();
					
				});
			},
		},

		mounted() {
			this.getUser();
			var user = sessionStorage.getItem('user');
			if (user) {
				user = JSON.parse(user);
				this.community = user.community;
			}
		}		
	}

</script>

<style scoped>

</style>


