<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="activities" highlight-current-row v-loading="listLoading" style="width: 100%;">
			<el-table-column type="index" width="50">
			</el-table-column>
			<el-table-column prop="theme" label="" width="150" sortable>
			</el-table-column>
			<el-table-column prop="content" label="" width="250"  sortable>
			</el-table-column>
			<el-table-column prop="ddl" label="" width="150" sortable>
			</el-table-column>
			<el-table-column prop="place" label="" width="160" sortable>
			</el-table-column>
			<el-table-column prop="poster.community" label="" width="150" sortable>
			</el-table-column>
			<el-table-column prop="status" label="" width="150" :formatter="formatStatus" sortable>
			</el-table-column>
			<el-table-column label="" width="250" >
				<template scope="scope">
					<el-button size="small" @click="handleView(scope.$index, scope.row)"></el-button>
					<el-button type="warning" size="small" @click="handleStop(scope.$index, scope.row)" :loading="deleteLoading"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)" :loading="deleteLoading"></el-button>
				</template>
			</el-table-column> 	
		</el-table>

		<!---->
		<el-dialog title="" v-model="FormVisible" :close-on-click-modal="false" style="width: 100%;">
	
			<el-table :data="signUpList" v-loading="SighLoading" ref="signUpList" style="width: 100%;">
				<el-table-column type="index" width="60">
				</el-table-column>
				<el-table-column prop="participant.nickName" label="" width="150" sortable>
				</el-table-column>
				<el-table-column prop="participant.gender" label="" width="100" :formatter="formatSex" sortable>
				</el-table-column>
				<el-table-column prop="participant.college" label="" min-width="180" sortable>
				</el-table-column>
				<el-table-column prop="participant.community" label="" min-width="180" sortable>
				</el-table-column>
				<el-table-column prop="participant.tel" label="" min-width="150" sortable>
				</el-table-column>
			</el-table>

			<el-container style="height:40px">
				<el-header style="text-align: center; font-size: 12px">
					<p>{{this.signUpList.length}} </p>
				</el-header>
			</el-container>
			
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="">
					<el-input v-model="addForm.theme" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input type='textarea' v-model="addForm.content" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="addForm.place" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="11">
						<el-date-picker type="date" placeholder="" v-model="addForm.date" style="width: 100%;"></el-date-picker>
					</el-col>
					<el-col class="line" :span="2">-</el-col>
					<el-col :span="11">
						<el-time-picker type="fixed-time" placeholder="" v-model="addForm.time" style="width: 100%;"></el-time-picker>
					</el-col>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
import {getActivitiesList,
		addActivity,
		deleteActivity,
		getSignList,
		stopActivity,
		} from '../../api/api';
import util from '../../common/js/util';
export default {
	data () {
		return {
			//
			activities:[],
			listLoading:false,

			//
			FormVisible:false,
			SighLoading:false,
			signUpList:[],

			//
			addLoading:false,
			addFormVisible:false,
			addForm:{
				theme:'',
				content:'',
				place:'',
				date:'',
				time:'',
				posterId:'',
				picture:'',
			},
			addFormRules:{
				theme: [
				{ required: true, message: "", trigger: "blur" }
				],
				content: [
				{ required: true, message: "", trigger: "blur" }
				],
				data: [
				{ required: true, message: "", trigger: "blur" }
				],
				place: [
				{ required: true, message: "", trigger: "blur" }
				],
			},

			//
			deleteLoading:false,
		}
	},

	methods: {
		//
		formatSex: function (row, column) {
			return row.participant.gender == "1" || row.gender == 1 ? '' : row.participant.gender == "0" || row.gender == 0 ? '' : '';
		},

		formatStatus: function (row, column) {
			return row.status == "1" ? '' : row.status == "0" ? '' : '';
		},

		//
		getActivities() {
			var user = sessionStorage.getItem('user');
			if (user) {
					user = JSON.parse(user);
				} 
			let para = {};
			this.listLoading = true;
			getActivitiesList(para).then((res) => {
				this.activities = res.data.activitys;
				this.listLoading = false;
			}).catch(() => {
				this.$message({
						message: "",
						type: "warning"
					});
			})
		},

		//
		handleAdd: function() {
			this.addFormVisible = true;
			var user = sessionStorage.getItem('user');
			if (user) {
					user = JSON.parse(user);
			}
			this.addForm.posterId = user.username;

		},

		//
		addSubmit: function() {
			this.$refs.addForm.validate(valid => {
				if (valid) {
					this.$confirm("", "", {}).then(() => {
						//this.addLoading = true;

						let para = Object.assign({}, this.addForm);
						para.date = (!para.date || para.date == '') ? '' : util.formatDate.format(new Date(para.date), 'yyyyMMdd');
						para.time = (!para.time || para.time == '') ? '' : util.formatDate.format(new Date(para.time), 'hhmm');
						para.ddl = para.date + para.time;
						console.log(para);
						addActivity(para).then(res => {
						this.addLoading = false;
						this.$message({
							message: (res.data.code == "1"	) ? "" : "",
							type: (res.data.code == "1"	) ? "success" : "error"
						});
						this.$refs["addForm"].resetFields();
						this.addFormVisible = false;
						this.getActivities();
					});
					})
				}
			})
		},

		//
		handleStop:function(index, row) {
			let para = {
				activityId: row._id,
			};
			this.deleteLoading = true;
			stopActivity(para).then((res) => {
				this.deleteLoading = false;
				this.$message({
						message: (res.data.code == "1"	) ? "" : "",
						type: (res.data.code == "1"	) ? "success" : "error"
					});
				this.getActivities();

			})
		},
		//
		handleView: function(index, row) {
			this.FormVisible = true;
			this.SighLoading = true;
			let para = {
				activityId: row._id,
			};
			getSignList(para).then((res) => {
				this.signUpList = res.data.confirm;
				this.SighLoading = false;
			});
		},

		//
		handleDel: function(index, row) {
			let para = {
				activityId: row._id,
			};
			this.deleteLoading = true;
			deleteActivity(para).then((res) => {
				this.deleteLoading = false;
				this.$message({
						message: (res.data.code == "1"	) ? "" : "",
						type: (res.data.code == "1"	) ? "success" : "error"
					});
				this.getActivities();

			})
		},

	},
	mounted() {
		this.getActivities();
	}
}
</script>



